.nav-bar{
    width: 100%;
    background-color: white;
    height: 70px;
    margin-bottom: 5px;
}
.custom-container{
    width: 80vw;
    margin: 0 auto;
}
.profile-details-nav{
    width:80vw;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    /* background-color: beige; */
}
.logout-pic-nav{
    display: inline-flex;
    align-items: center;
    gap:1rem;
}

.user-logout-img{
    width: 2.2rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    overflow: hidden;
}

.logout-btn{
    display: inline-block;
    font-weight: 500;
    padding:var(--btn-padding);
    border-radius: var(--border-radius);
    cursor: pointer;
    transition: all 300ms ease;
    font-size: 0.7rem;
    background-color: var(--color-primary);
    color: var(--color-white);
    width: fit-content;
    text-align: center;
}
.logout-btn:hover{
    opacity: 0.8;
}