@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Engagement&family=Montserrat:ital,wght@0,100;0,200;0,400;0,500;1,100;1,200;1,300;1,400&family=Poppins:ital,wght@0,300;0,400;0,700;1,400&display=swap');


:root{
  --color-white : hsl(252, 30%, 100%);
  --color-light: hsl(252, 30%, 95%);
  --color-gray : hsl(252, 15%, 65%);
  --color-primary: hsl(252,75%, 60%);
  --color-dark: hsl(0,0,0);

  --border-radius: 2rem;
  --btn-padding: 0.6rem 2rem; 
  --card-padding: 1rem; 
  --card-border-radius:1rem;
  --sticky-top-left: 5.6rem;
  --sticy-top-right : -18rem;
}



body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  position: relative;
  background-color: whitesmoke;
  text-decoration: none;
}

code {
  font-family: 'Poppins', sans-serif;
}

*, *::after , *::before{
  padding: 0;
  margin:0;
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  outline:none;
}

.profile{
  text-decoration: none;
  color: black;
}
.profile-photo{
  width: 2.7rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}
img{
  display: block;
  width:100%;
}
a{
  text-decoration: none;
  color: black;
}

.custom-btn{
  display: inline-block;
  padding : var(--btn-padding) ;
  font-weight: 500;
  border-radius: var(--border-radius);
  cursor: pointer;
  transition: all 300ms ease;
  font-size: 0.9rem;
  background-color: var(--color-primary);
  color: var(--color-white);
  margin-top: 1rem;
  width: 200px;
  text-align: center;
  margin-left: 2rem;
 
}

.custom-btn:hover{
  opacity: 0.8;

}
/* -----------------HOME------------- */
.home, .dashboard-main, .recognize-page{
  position: relative;
  top: 80px;
  
}
.dashboard-main .custom-container, .recognize-page  .custom-container{
  display: grid;
  grid-template-columns: 300px auto 300px ;
  column-gap: 2rem;
  position: relative;
}
.home .custom-container{
  display: grid;
  grid-template-columns: auto 300px ;
  column-gap: 2rem;
  position: relative;
}

.left{
  position:static;
}

.home .custom-container .left, .dashboard-main .custom-container .left,  .recognize-page .custom-container .left{
  height: max-content;
  position: sticky;
  top : var(--sticky-top-left)

}
.home .custom-container .left .profile,
.dashboard-main .custom-container .left .profile ,
.recognize-page .custom-container .left .profile{
 background: var(--color-white);
 display: flex;
 align-items: center;
 column-gap: 1rem;
 width: 250px;
 padding:var(--card-padding);
 border-radius: var(--border-radius);
}

/* ---------------MAIN SECTION ----------------- */
main{
  position: relative;
  top: 80px;
}

main .custom-container{
  display: grid;
  grid-template-columns: 300px auto 300px ;
  column-gap: 2rem;
  position: relative;
}
/* -----------------LEFT--------------------------- */

main .custom-container .left{
  height: max-content;
  position: sticky;
  top : var(--sticky-top-left)

}
main .custom-container .left .profile{

 background: var(--color-white);
 display: flex;
 align-items: center;
 column-gap: 1rem;
 width: 250px;
 padding:var(--card-padding);
 border-radius: var(--border-radius);
}

.left .sidebar{
  margin-top: 1rem;
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  width: 250px;
}

 .left .sidebar .menu-item{
  display: flex;
  align-items: center;
  height: 4rem;
  cursor: pointer;
  transition: all 300ms ease;
  position: relative;
}

.left .sidebar .menu-item:hover{
  background: var(--color-light);
}

.left .sidebar i{
  font-size: 1.4rem;
  color : var(--color-gray);
  margin-left: 2rem;
  position: relative;
}

#notification-count{
  background: red;
  color: white;
  font-size: 0.7rem;
  width: fit-content;
  border-radius: 0.8rem;
  padding: 0.1rem 0.4rem;
  position: absolute;
  top: 0.4rem;
  left: 2.3rem;
}
.left .sidebar .menu-item h5{
  font-size: 1rem;
  margin-left: 1.2rem;
}
.left .sidebar .active{
  background-color: var(--color-light);
}
.left .sidebar .active i, .left .sidebar .active h5{
  color:var(--color-primary)
}

.left .sidebar .active::before{
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  width: 0.5rem;
  background: var(--color-primary)
}
.notifications-popups{
  background: yellow;
  margin-left :270px;
  position: absolute;
  top: 14%;
  width: 30rem;
  background-color: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  box-shadow: 0 0 2rem hsl(var(--color-primary), 75%, 60% 25%);
  z-index: 1;
  display: show;
}
.notifications-popups::before{
  content: '';
  width: 1.2rem;
  height: 1.2rem;
  display: block;
  position: absolute;
  left:-0.6rem;
  transform: rotate(45deg);
  background: var(--color-white);
  z-index: 1;
}

.notifications-popups > div{
  display: flex;
  align-items: start;
  gap: 1rem;
  margin-bottom: 1rem;
  z-index: 1;
}

.notification-body small{
  display: block;
  color:red;
  text-transform: uppercase;
}
a:hover{
  color:inherit;
}
.left .sidebar .menu-item:first-child.active{
  border-top-left-radius: var(--card-border-radius);
  overflow: hidden;
}
.left .sidebar .menu-item:last-child.active{
  border-top-left-radius: var(--card-border-radius);
  overflow: hidden;
}

.recognitions-section{
  margin-top:10px;
  display: flex;
  background: var(--color-white);
  border-radius: var(--card-border-radius);

}

.recognitions-section .recognition-header{
  border-bottom: 1px solid var(--color-primary);
  width:100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding : var(--btn-padding)
}

.recognitions-section .recognition-header h5{
  font-size: 18px;
  font-weight: 100;
}

/* ----------------------MIDDLE------------------- */
.middle{
  width:100%;
  position: relative;

}
.like-icon{
  transition: all 300ms ease;
}

.like-icon:hover{
  color : var(--color-primary);
  transform: rotateY(180deg);
}

.middle .create-post{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 1rem;
  background: var(--color-white);
  padding: 0.4rem var(--card-padding);
  border-radius:var(--border-radius);
}

.middle .create-post input[type="text"]{
  justify-self: start;
  width: 100%;
  padding-left: 1rem;
  background: transparent;
  color: var(--color-dark);
  margin-right: 1rem;
}

.middle .feeds .feed{
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  margin: 1rem 0;
  font-size: 0.85rem;
  line-height: 1.5;
}



 .middle .feed .user{
  display: flex;
  align-items: center;
  gap:1rem;
} 
.middle .feed .user .info .name{ 
font-size: 1rem;
}

.feeds{
  z-index: -1;
}

.feed .action-buttons{
  display: flex;
  justify-content: space-between;
  font-size: 25px;
  align-items: center;
  margin: 10px;
}


.middle .liked-by{
  display: flex;
}

.middle .liked-by span{
  width: 1.4rem;
  height: 1.4rem;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--color-white);
  margin-left: -0.6rem;
}
.middle .liked-by span:first-child{
  width: 1.4rem;
  height: 1.4rem;
  display: block;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--color-white);
  margin-left:0;
}

.feed .comments-section{
  width:100%;
  min-height: fit-content;
  border-top: 1px solid #e6e6e6;
}

.feed .comments-section .comment .comment-user{
  width:1.2rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}

.feed .comments-section .comment{
  display: inline-flex;
  align-items: center;
  gap:0.2rem;
  width:100%;
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  margin-top:10px;
  padding-bottom: 10px;
  background: var(--color-light);

} 
.feed .comments-section .comment .commented-by
{
  font-size: small;
  font-weight: bold;
}
.feed .comments-section .comment .comment-content{
  font-size: small;
}
.feed .comments-section .comment-on-post-user{
  width:1.8rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}
.comment-button{
  width:100px;
  height: 30px;
  padding-left:14px;
  font-size: 0.6rem;
  margin-left: 0;
  text-transform: uppercase;
}
.feed .comments-section .comment-on-post .comment-on-post-user-comment{
width:80%;
border-bottom: 1px solid #e6e6e6;
}

.feed .comments-section .comment-on-post{
  display:inline-flex;
  align-items: flex-end;
  gap:1rem;
  width:100%;

}


.feed .head .user .profile-photo{
  width: 3rem;
  aspect-ratio: 1/1;
}

/* -------------RIGHT---------- */
.leaderboard{
  position: fixed;
}
.leaderboardPoints{
  display: flex;
  justify-content: space-evenly;
  position: relative;
  
}
.points{
  background: var(--color-white);
  padding: 8px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: var(--card-border-radius);
  width:140px;

  
}
.recognition-points{
  font-size: 30px;
  color: var(--color-primary);

}

.recognition-top-scorers{
  display: flex;
  flex-direction: column;
  gap:1rem;
  background-color: var(--color-white);
  margin-top: 1rem;
  border-radius: var(--card-border-radius);
  height: 550px;
  overflow-y: scroll;
  transition: all 300ms ease;

}
.recognition-top-scorers::-webkit-scrollbar {
  width: 4px;
}

.recognition-top-scorers::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 100px;
}

.recognition-top-scorers::-webkit-scrollbar-thumb {
  background-color: var(--color-primary);
  border-radius: 100px;
}


.recognition-top-scorers .person{
  display: flex;
  align-items: start;
  background: var(--color-white);
  width: 100%;
  padding: 10px;
}

.recognition-top-scorers .person:hover{
  background: var(--color-light);

}
.recognition-top-scorers .person .user{
  display: flex;
}
.recognition-top-scorers .person .info{
  margin-left: 1rem;
  width: 210px;
}
.recognition-top-scorers .person .info .username{
  position: relative;
  top:8px;
  font-weight: bold;
  font-size: 0.8rem;
} 
.recognition-top-scorers .person .info small{
  font-size: 0.8rem;
}
.recognition-top-scorers .person .user .profile-photo{
  width: 3rem;
}


/* ----------------DASHBOARD---------------- */
.dashboard .dashboard-points{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.dashboard .dashboard-leaderboard{
  background-color: var(--color-white);
  width: 700px;
  position: relative;
  margin: 0 auto;

  border-radius: var(--card-border-radius);

}
.dashboard-leaderboard .dashboard-leaderboard-head{
  /* border-bottom: 1px solid var(--color-primary); */
  padding: var(--card-padding);

}

.dashboard-leaderboard .dashboard-leaderboard-head .leaderboard-trophy{


}
.dashboard-leaderboard .dashboard-leaderboard-head .title{
  font-weight: 500;
  font-size: 20px;
  position: absolute;

}
.dashboard-leaderboard-members{
position: relative;
display: grid;
grid-template-columns: repeat(2, auto);

}

.dashboard-leaderboard-members .person{
  margin:3px;
  width: 320px;
  padding: var(--card-padding);

}
.dashboard-leaderboard-members .person .user .profile-photo{
  width: 3rem;
}
.dashboard-leaderboard-members .person .user{
display: flex;
flex-direction: row;

}
.dashboard-leaderboard-members .person .user .info{
margin-left: 12px;


}

.dashboard-leaderboard-members .person .user .info .username{
  font-size: smaller;
  font-weight: 600;
  letter-spacing: -0.5px;

}
.dashboard-leaderboard-members .person .user .info .point{
color : var(--color-primary);
font-weight: bold;

}
.dashboard-leaderboard-members .person .user .info small{
font-size: small;
letter-spacing: -0.4px;
}

.dashboard .dashboard-my-recognitions{

  width:100%;
}
.dashboard-my-recognitions .dashboard-my-recognitions-head .title h3{
  font-size: 20px;
}

.recognitions .feed{
  background: var(--color-white);
  border-radius: var(--card-border-radius);
  padding: var(--card-padding);
  margin: 1rem 0;
  font-size: 0.85rem;
  line-height: 1.5;
  width:100%;
}



.recognitions .feed .user{
  display: flex;
  align-items: center;
  gap:1rem;

} 
.recognitions .feed .user .info .name{ 
font-weight: 600;
color: var(--color-primary)
}


.recognitions .feed .head .user .profile-photo{
  width: 2rem;
  aspect-ratio: 1/1;
}

.recognitions .feed:hover{
  background-color: var(--color-light);
  transition: all 300ms ease;

}

/* ------------Recognize-------- */

.recognize{
  width: 100%;

}
.awards-section{
  margin-top: 5rem;
  margin-left: 20px;

}

.recognize-badges{
  display: grid;
  grid-template-columns: repeat(1, auto);
  padding: relative;
  align-items: center;
  gap:2rem;
  margin-bottom: 5rem;
}

.badge{
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center; 
  position: relative;
  transition: all 1s ease;
  background: white;
  cursor: pointer;
  
}
.badge:hover{
  /* transform: translateY(-10px); */
  transform: rotateY(180deg);
  background-color: var(--color-light);
}

.badge1{
  width: 150px;
  height: 88px;
  background-color: orange;
  position: relative;
}
.badge1::before{
  content:'';
  width: inherit;
  height: inherit;
  background-color: orange;
  position: absolute;
  top:0;
  left:0;
  transform:rotate(60deg);
  

}
.badge1::after{
  content:'';
  width: inherit;
  height: inherit;
  background-color: orange;
  position: absolute;
  top:0;
  right:0;
  transform:rotate(-60deg);

}

