  position: relative;
  width:100%;
  height:100%;
  background-color: #ee573a;
}

.badge-1-section .award-name .award-name-content::before{
  content:'';
  position: absolute;
  top:-10px;
  left: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid #ca2f11;
  border-top: 5px solid transparent;
  border-bottom: 5px solid #ca2f11;
}

.badge-1-section .award-name .award-name-content::after{
  content:'';
