  top:-10px;
  right: 0;
  border-left: 5px solid #ca2f11;
  border-right: 5px solid transparent;
  border-top: 5px solid transparent;
  border-bottom: 5px solid #ca2f11;
}

.badge .award-name .award-name-content .name{
  font-size: 15px;
  padding-top:10px;
}


