.recognize-section{
  max-width: 100%;
  background-color: var(--color-white);
  padding : var(--card-padding);
  border-radius: var(--card-border-radius);
  outline: none;
}
.recognize-section .input-box{
  padding-top: 10px;
  border-bottom: 1px solid #e6e6e6;
 
}
.recognize-section .input-area{
  position: relative;
  min-height: 30px;
}

.recognize-section .input-placeholder{
  position: absolute;
  margin-top: -3px;
  font-size: 22px;
  color: #98a5b1;
}

.input-area .input{
  
  outline: none;
  font-size: 17px;
  word-wrap: break-word;
}

.input-area .readonly{
  position: absolute;
  top:0;
  left: 0;
  z-index: -1;
  
}

.input-box .post-privacy{
  color: var(--color-primary);
  margin :15px 0;
  padding: 7px 10px;
  display: inline-flex;
  align-items: center;
  transition: all 0.2s ease;
  border-radius: 50px;
}
.input-box .post-privacy:hover{
  opacity: 0.8;
  cursor: pointer;
}
.post-privacy i{
  font-size: 18px;;
}
.post-privacy span{
  font-size: 15px;
  font-weight: 600;
  margin-left: 7px;
}

.recognize-section .bottom{
display: flex;
margin-top: 13px;
align-items: center;
justify-content: space-between;

}

.bottom .post-icons{
  display: inline-flex;
  margin-left: 0;
  margin-left: -20px;

}

.post-icons li{
  list-style: none;
  height: 38px;
  width:38px;
  background: #e7f5fe;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;

  font-size: 20px;
 
}

.recognized-post-person .recognized-post-person-img{
  width: 1.8rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
}
.recognized-post-person{
position: relative;
}

.recognize-post-message{
 min-height: 100px;
  width: 100%;

}

.person-suggestion-close-mark{
  font-size: 20px;
  top:-6px;
  position: absolute;
  color: var(--color-gray)
}
.person-suggestion-close-mark:hover{
  color:rgb(209, 23, 23);
}

.recognized-post-person-content{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0.4rem;
  padding :3px 9px 3px 9px;
  border-radius: 50px;
  font-size: 12px;
  background-color: var(--color-light);
  transition: all 300ms ease;

}

.recognized-post-person-content:hover{
background-color: var(--color-gray);
}


.recognized-post-person{
  width: 100%;


}
.icons-selection{
  display: inline-flex;
  width:100%;
  gap: 0.4rem;
  padding:0;
  padding-top:10px;
  border-top: 1px solid #e6e6e6;
}
.icons-selection li{
  list-style: none;
  border-radius: 50%;
  font-size: 25px;
}

.icons-selection li i{

}
.icons-selection li i:hover{
  background-color: var(--color-light);
  color: var(--color-primary)
}

.selections{
  position: relative;

}
.person-selection{
  width:400px;
  height: fit-content;
  position: absolute;
  background: var(--color-white);
  margin-top:10px;
  border : 1px solid #e6e6e6;
