  position: absolute;
  top:-8px;
  left:10px;
  border-top: 8px solid var(--color-light);
  border-right: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 8px solid var(--color-light);
  transform: rotate(45deg);
 

}

